<template>
    <div>
        <div style="margin-bottom: 5px;">
            <el-input v-model="form.username" placeholder="请输入用户名" suffix-icon="el-icon-search" style="width: 200px;"
                      @keyup.enter.native="listPGet"></el-input>
            <el-button type="primary" style="margin-left: 8px" @click="listPGet">查询</el-button>
            <el-button type="success" @click="resetParam">重置</el-button>
            <el-button type="primary" style="margin-left: 8px" @click="add">新增</el-button>
        </div>
        <el-table
                :data="tableData"
                :header-cell-style="{background: '#f2f5fc',color: '#555'}"
                border>
            <el-table-column prop="userid" label="UserID" width="200">
            </el-table-column>
            <el-table-column prop="username" label="用户名" width="200">
            </el-table-column>
            <el-table-column prop="fullname" label="全名" width="200">
            </el-table-column>
            <el-table-column prop="address" label="地址" width="240">
            </el-table-column>
            <el-table-column prop="contactnumber" label="联系电话" width="240">
            </el-table-column>
            <el-table-column prop="isvalid" label="操作">
                <template slot-scope="scope">
                    <el-button size="small" type="success" @click="mod(scope.row)">编辑</el-button>
                    <el-popconfirm
                            title="确定删除吗？"
                            @confirm="del(scope.row.userid)"
                            style="margin-left: 5px"
                    >
                        <el-button slot="reference" size="small" type="danger">删除</el-button>
                    </el-popconfirm>
                    <!--                <el-button size="small" type="danger" @click="del(scope.row.id)">删除</el-button>-->
                </template>
            </el-table-column>
        </el-table>
        <el-dialog
                title="提示"
                :visible.sync="centerDialogVisible"
                width="30%"
                center>
            <el-form ref="form" :model="form" label-width="80px">
                <el-form-item label="UserID" prop="userid">
                    <el-col :span="20">
                        <el-input v-model="form.userid"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label="用户名" prop="username">
                    <el-col :span="20">
                        <el-input v-model="form.username"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-col :span="20">
                        <el-input v-model="form.password"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label="全名" prop="fullname">
                    <el-col :span="20">
                        <el-input v-model="form.fullname"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label="地址" prop="address">
                    <el-col :span="20">
                        <el-input v-model="form.address"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label="电话" prop="contactnumber">
                    <el-col :span="20">
                        <el-input v-model="form.contactnumber"></el-input>
                    </el-col>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
    <el-button @click="centerDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="save">确 定</el-button>
  </span>
        </el-dialog>
    </div>


</template>

<script>
    export default {
        name: "UserManage",
        data(){
            return{
                tableData:[],
                centerDialogVisible: false,
                form:{
                    userid: '',
                    username: '',
                    password: '',
                    fullname: '',
                    address: '',
                    contactnumber: ''
                }
            }
        },
        methods: {
            //刷新表单
            resetForm() {
                this.$refs.form.resetFields();
            },
            add() {
                this.centerDialogVisible = true
                this.$nextTick(()=>{
                    //不能直接调用，要用异步的方法
                    this.resetForm();
                })
                this.resetForm();
            },
            //添加
            doSave(){
                this.$axios.post(this.$httpUrl + '/users/save', this.form).then(res => res.data).then(res => {
                    this.centerDialogVisible = true
                    console.log(res)
                    if (res) {
                        this.$message({
                            showClose: true,
                            message: '操作成功！',
                            type: 'success'
                        });
                        this.centerDialogVisible=false
                        this.loadGet()
                    } else {
                        this.$message({
                            showClose: true,
                            message: '操作失败！',
                            type: 'error'
                        });
                    }
                })
            },
            //修改
            doMod(){
                this.$axios.post(this.$httpUrl + '/users/mod', this.form).then(res => res.data).then(res => {
                    if (res) {
                        this.$message({
                            showClose: true,
                            message: '操作成功！',
                            type: 'success'
                        });
                        this.centerDialogVisible=false
                        this.loadGet()
                    } else {
                        this.$message({
                            showClose: true,
                            message: '操作失败',
                            type: 'error'
                        });
                    }
                })
            },
            //添加或修改
            save() {
                this.$refs.form.validate((valid) => {
                    if(valid){
                        if(this.form.userid){
                            this.doMod();
                        }else{
                            this.doSave();
                        }
                    }else{
                        console.log('error submit!!')
                    }
                });
            },
            //查询
            loadGet() {
                this.$axios.get(this.$httpUrl + '/users/list').then(res => {
                    if (res.status === 200) {
                        console.log(res)
                        this.tableData = res.data
                    } else {
                        alert('获取数据失败')
                    }
                })
            },
            //根据用户名查询
            listPGet() {
                this.$axios.post(this.$httpUrl + '/users/listP',this.form).then(res => {
                    if(res.status === 200){
                        this.tableData = res.data
                    }else{
                        alert('获取数据失败')
                    }
                })
            },
            //删除
            del(userid){
                this.$axios.get(this.$httpUrl + '/users/delete?userid='+userid).then(res => res.data).then(res => {
                    if (res) {
                        this.$message({
                            showClose: true,
                            message: '操作成功！',
                            type: 'success'
                        });
                        this.loadGet()
                    } else {
                        this.$message({
                            showClose: true,
                            message: '操作失败！',
                            type: 'error'
                        });
                    }
                })
            },
            //修改时获取到表单的数据
            mod(row){
                this.centerDialogVisible = true
                this.$nextTick(()=>{
                    //异步的方法
                    this.form.userid = row.userid
                    this.form.username = row.username
                    this.form.password = ''
                    this.form.fullname = row.fullname
                    this.form.address = row.address
                    this.form.contactnumber = row.contactnumber
                })
            },
            resetParam() {
                this.form.username = ''
            }
        },
        beforeMount() {
            this.loadGet();
        }
    }
</script>

<style scoped>

</style>